<template>
  <div class="container">
    <el-card>
      <el-row>
        <el-col :span="12">
          请选择安装的语言:
          <el-select v-model="formData.selectedLanguage" placeholder="Select" @change="sendWebSocketRequest">
            <el-option
                v-for="item in languageList"
                :key="item.id"
                :label="item.language"
                :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="12" class="right-col">
          <el-button type="primary" :disabled="isInstallButtonDisabledComputed" @click="install">安装</el-button>
        </el-col>
      </el-row>
      <div class="spacer"></div> <!-- Add a spacer -->
      <el-card class="box-card" style="overflow-y: auto; max-height: 470px; min-height: 470px;">
        <div v-if="websocketContentArray.length === 0">没有安装日志信息</div>
        <div v-for="contentText in websocketContentArray.slice().reverse()" :key="contentText">
          <el-card>
            <el-descriptions class="margin-top" :column="3"  border>
              <el-descriptions-item label="时间">{{ parseJson(contentText).time }}</el-descriptions-item>
              <el-descriptions-item label="服务器名称">{{ parseJson(contentText).name }}</el-descriptions-item>
              <el-descriptions-item label="状态">{{ parseJson(contentText).state }}</el-descriptions-item>
              <el-descriptions-item label="消息">{{ parseJson(contentText).message }}</el-descriptions-item>
            </el-descriptions>
          </el-card>
        </div>
      </el-card>
    </el-card>
  </div>
</template>

<style>
.text {
  font-size: 14px;
}

.item {
  padding: 30px 0;
}

.box-card {
  width: 100%;
  margin-top: 20px; /* Adjust the margin as needed */
}

.right-col {
  display: flex;
  justify-content: flex-end;
}

.spacer {
  height: 50px; /* Adjust the height as needed */
}
</style>



<script>
import axios from 'axios';
const host = window.location.host; // 获取主机地址，例如：127.0.0.1:6005
const protocol = window.location.protocol; // 获取协议，例如：http 或 https

// 构建完整的服务地址
const serviceAddress = protocol + '//' + host;
export default {
  data() {
    return {
      formData: {
        selectedLanguage: null,
      },
      languageList: [],
      websocketContentArray: [], // 用于存储WebSocket内容的数组
      websocket: null, // WebSocket实例
      maxContentArrayLength: 1000, // 最大消息数量
      isInstallButtonDisabledComputed: true,
    };

  },
  computed: {
    // eslint-disable-next-line vue/no-dupe-keys
    isInstallButtonDisabledComputed() {
      // Disable the button if WebSocket is not connected or if no language is selected
      return !(this.websocket && this.websocket.readyState === WebSocket.OPEN && this.formData.selectedLanguage);
    },
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        const response = await axios.get(serviceAddress+'/getLanguageInstallConfig');
        this.languageList = response.data.data;
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },
    connect() {
      // 建立WebSocket连接
      this.websocket = new WebSocket('ws://'+host+'/installs');

      // 处理WebSocket事件
      this.websocket.addEventListener('open', () => {
        this.isInstallButtonDisabledComputed = false;
      });

      this.websocket.addEventListener('message', (event) => {
        // 将新消息添加到数组中
        this.websocketContentArray.push(event.data);

        // 维护数组的长度，以确保不超过最大限制
        if (this.websocketContentArray.length > this.maxContentArrayLength) {
          this.websocketContentArray.shift(); // 移除最旧的消息
        }
      });

      this.websocket.addEventListener('close', () => {
        console.log('WebSocket closed');
      });

      this.websocket.addEventListener('error', (error) => {
        console.error('WebSocket error:', error);
      });
    },
    async install() {
      // 检查WebSocket是否已连接
      if (this.websocket && this.websocket.readyState === WebSocket.OPEN) {
        // 发送安装命令
        if (this.formData.selectedLanguage) {
          const requestData = {
            languageId: this.formData.selectedLanguage,
          };

          this.websocket.send(JSON.stringify(requestData));
        } else {
          console.error('Selected language is null.');
        }
      } else {
        console.error('WebSocket is not connected.');
      }
    },
    sendWebSocketRequest() {
      this.connect();
      this.isInstallButtonDisabledComputed = true;
    },parseJson(jsonText) {
      try {
        return JSON.parse(jsonText);
      } catch (error) {
        console.error('Error parsing JSON:', error);
        return null;
      }
    },

  },
};
</script>
