<template>
  <el-card class="chart-card">
    <div class="chart-container">
      <div id="memoryChart" style="height: 400px;"></div>
    </div>
  </el-card>
</template>

<script>
import {inject, onMounted, ref, watch} from "vue";
import * as echarts from 'echarts';

export default {
  setup() {
    const serverData = inject('serverData'); // 从父组件注入数据
    const memoryChart = ref(null);

    // 组件被挂载到 DOM 后执行
    onMounted(() => {
      memoryChart.value = echarts.init(document.querySelector('#memoryChart'));
      updateChart();
    });

    // 当 serverData 更新时，更新图表
    watch(() => serverData.value, (newData) => {
      if (newData) {
        updateChart();
      }
    }, {immediate: true});

    // 更新图表的方法
    function updateChart() {
      if (!memoryChart.value || !serverData.value||!Array.isArray(serverData.value)) {
        return;
      }

      const processedData = processData(serverData.value);
      const options = {
        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%'];
          }
        },
        title: {
          left: 'right',
          text: '内存使用量（MB）',
          subtext: processedData.subTitle,
        },
        xAxis: {
          type: 'time',
          boundaryGap: false
        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%'],
          max: processedData.memoryTotalMb,
          name: 'MB'
        },
        series: [{
          name: 'Memory Usage (MB)',
          type: 'line',
          smooth: true,
          symbol: 'none',
          areaStyle: {},
          data: processedData.chartData
        }]
      };

      memoryChart.value.setOption(options);
    }

    // 处理 serverData 来获取图表数据
    function processData(rawData) {
      const totalMb = rawData[0]?.memoryTotalMb || 0;
      const lastData = rawData[rawData.length - 1] || {};
      const usedMb = lastData.memoryUsedMb || 0;
      const usagePercent = (usedMb / totalMb * 100).toFixed(2);
      // 向上取整
      const roundedTotalMb = Math.ceil(totalMb);

      return {
        chartData: rawData.map(item => [item.createTimeFormatted, item.memoryUsedMb]),
        subTitle: `当前: ${usagePercent}% 使用: ${usedMb.toFixed(2)}MB 总共: ${roundedTotalMb}MB`,
        memoryTotalMb: roundedTotalMb
      };
    }

    return {
      memoryChart
    };
  }
};
</script>

<style>
.chart-card {
  margin: 10px;
}

.chart-container {
  width: 100%;
  height: 400px;
}
</style>
