<template>
  <el-card class="chart-card">
    <div class="chart-container">
      <div id="networkIoChart" style="height: 400px;"></div>
    </div>
  </el-card>
</template>

<script>
import { inject, onMounted, ref, watch } from "vue";
import * as echarts from 'echarts';

export default {
  setup() {
    const serverData = inject('serverData'); // 从父组件注入数据
    const networkIoChart = ref(null);

    onMounted(() => {
      networkIoChart.value = echarts.init(document.querySelector('#networkIoChart'));
      updateChart();
    });

    watch(() => serverData.value, (newData) => {
      if (newData) {
        updateChart();
      }
    }, { immediate: true });

    function updateChart() {
      if (!networkIoChart.value || !serverData.value||!Array.isArray(serverData.value)) {
        return;
      }

      const processedData = processData(serverData.value);
      const options = {
        title: {
          text: '网络使用（Mbps）',
          subtext: `下载: ${processedData.latestDownload}Mbps 上传: ${processedData.latestUpload}Mbps`,
          left: 'right'
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: processedData.downloadData.map(item => item[0])
        },
        yAxis: {
          name: 'Mbps',
          type: 'value'
        },
        series: [
          {
            name: '下载Mbps',
            type: 'line',
            data: processedData.downloadData.map(item => item[1]),
            smooth: true
          },
          {
            name: '上传Mbps',
            type: 'line',
            data: processedData.uploadData.map(item => item[1]),
            smooth: true
          }
        ]
      };

      networkIoChart.value.setOption(options);
    }

    function processData(rawData) {
      const latestData = rawData[rawData.length - 1] || {};
      const downloadData = rawData.map(item => [item.createTimeFormatted, parseFloat(item.networkDownloadMbps.toFixed(2))]);
      const uploadData = rawData.map(item => [item.createTimeFormatted, parseFloat(item.networkUploadMbps.toFixed(2))]);
      return {
        downloadData,
        uploadData,
        latestDownload: parseFloat((latestData.networkDownloadMbps || 0).toFixed(2)),
        latestUpload: parseFloat((latestData.networkUploadMbps || 0).toFixed(2))
      };
    }

    return {
      networkIoChart
    };
  }
};
</script>

<style>
.chart-card {
  margin: 10px;
}

.chart-container {
  width: 100%;
  height: 400px;
}
</style>
