<template>
  <div id="app">
    <el-container>
      <el-main>
        <el-card class="box-card">
          <div slot="header" class="clearfix"
               style="display: flex; justify-content: space-between; align-items: center;">
            <div style="display: flex; gap: 15px;">
              <el-select v-model="selectedServerId" @change="fetchSelect">
                <el-option
                    v-for="item in serverOptions"
                    :key="item.id"
                    :label="item.name"
                    :value="item.keyId">
                </el-option>
              </el-select>
              <el-dropdown>
                <el-button type="primary">
                  系统设置<i class="el-icon-arrow-down el-icon--right"></i>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item @click.native="showAddServerDialog">添加服务</el-dropdown-item>
                  <el-dropdown-item @click.native="showDeleteServerDialog">删除服务</el-dropdown-item>
                  <el-dropdown-item @click.native="showAddEnvironmentDialog">添加环境</el-dropdown-item>
<!--                  <el-dropdown-item @click.native="showAddEnvironmentDialog">设置参数</el-dropdown-item>-->
<!--                  <el-dropdown-item @click.native="showBackupDataDialog">备份数据</el-dropdown-item>-->
<!--                  <el-dropdown-item @click.native="showBackupDataDialog">导入数据</el-dropdown-item>-->
                </el-dropdown-menu>
              </el-dropdown>
            </div>
            <h1 style="text-align: center; flex: 1;">
              代码沙盒服务器后台性能监测系统
            </h1>
            <div style="display: flex; gap: 8px;">
              <el-button type="primary" icon="el-icon-video-play"
                         :disabled="isStartButtonDisabled"
                         @click="handleStartClick">启动
              </el-button>
              <el-button type="primary" icon="el-icon-video-pause"
                         :disabled="isStopButtonDisabled"
                         @click="handleStopClick">停止
              </el-button>
            </div>
          </div>
          <el-row class="content-container" ref="contentRow" v-if="dataLoaded">
            <el-col :span="8" class="info-column" style="width: 100%;">
              <systemInfo ref="infoCard"/>
            </el-col>
            <el-col :span="16" style="width: 100%;">
              <el-row>
                <el-col :span="12" v-if="dataLoaded">
                  <cpu/>
                </el-col>
                <el-col :span="12">
                  <memory/>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12">
                  <disk/>
                </el-col>
                <el-col :span="12">
                  <net/>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
          <el-empty :image-size="200" description="请点击系统设置添加服务器" v-else></el-empty>
        </el-card>
      </el-main>
    </el-container>
    <!-- 弹框组件 -->
    <el-dialog title="添加服务器" :visible.sync="isAddServerDialogVisible">
      <el-form label-position="top">
        <el-row :gutter="20" type="flex" justify="center">
          <el-col :span="10">
            <el-form-item label="IP 地址">
              <div class="ip-input-group">
                <el-input
                    v-for="index in 4"
                    :key="index"
                    v-model="ipSegments[index - 1]"
                    maxlength="3"
                    ref="ipInputs"
                    @input="handleIPInput(index - 1)"
                    @paste.native="handlePaste"
                    @keydown.native="handleBackspace($event, index - 1)"
                    class="ip-input">
                </el-input>
              </div>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <el-form-item label="端口号">
              <el-input v-model="serverPort"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="日志信息">
          <el-input type="textarea" v-model="logInfo" :rows="4" readonly></el-input>
        </el-form-item>
        <el-row>
          <el-col :span="24">
            <el-button type="primary" @click.native="addServer">添加</el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
    <el-dialog title="删除服务" :visible.sync="isDeleteServerDialogVisible">
      <el-table :data="serverlistData" style="width: 100%">
        <el-table-column prop="keyId" label="ID" width="180"></el-table-column>
        <el-table-column prop="name" label="服务器名称"></el-table-column>
        <el-table-column prop="baseUrl" label="基础URL"></el-table-column>
        <el-table-column prop="state" label="状态" width="100"></el-table-column>
        <el-table-column prop="level" label="等级" width="100"></el-table-column>
        <el-table-column prop="frequency" label="频率" width="100"></el-table-column>
        <el-table-column label="操作">
          <template v-slot="{row}">
            <el-popover
                placement="top"
                width="160"
                v-model="row.visible"
            >
              <p>删除仅仅删除数据库的数据，如果沙盒服务没有关闭，那么删除是无法自动删除Docker实例。</p>
              <div style="text-align: right; margin: 0">
                <el-button size="mini" type="text" @click="row.visible = false">取消</el-button>
                <el-button type="primary" size="mini" @click="() => confirmDelete(row.keyId)">确定</el-button>
              </div>
              <el-button slot="reference" type="danger" size="mini">删除</el-button>
            </el-popover>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog title="添加环境" :visible.sync="isAddEnvironmentDialogVisible" >
      <LanguageConfig/>
    </el-dialog>
  </div>
</template>

<script setup>
import {ref, onMounted, watchEffect, provide} from 'vue';
import systemInfo from "@/components/systemInfo.vue";
import Cpu from "@/components/cpu.vue";
import Memory from "@/components/memory.vue";
import Disk from "@/components/disk.vue";
import Net from "@/components/net.vue";
import LanguageConfig from "@/components/languageconfig.vue";

const selectedServerId = ref(null);
const serverData = ref({});
const systemConfigData = ref({});
const serverOptions = ref([]);
const dataLoaded = ref(false);
const isStartButtonDisabled = ref(true);
const isStopButtonDisabled = ref(true);

const host1 = window.location.host; // 获取主机地址，例如：127.0.0.1:6005
const protocol1 = window.location.protocol; // 获取协议，例如：http 或 https

// 构建完整的服务地址
const serviceAddress1 = protocol1 + '//' + host1;

provide('serverData', serverData);
provide('systemConfigData', systemConfigData);
onMounted(() => {
  fetchServerOptions();
  setInterval(fetchData, 60000); // 每60秒自动获取数据
  setInterval(fetchSystemConfigData, 60000); // 每60秒自动获取配置数据
});

watchEffect(() => {
  isStartButtonDisabled.value = systemConfigData.value.state !== 0;
  isStopButtonDisabled.value = !(systemConfigData.value.state === 1);
});


function fetchServerOptions() {
  axios.get(serviceAddress+'/serverInfo')
      .then(response => {
        serverOptions.value = response.data.data;
        if (serverOptions.value.length > 0) {
          selectedServerId.value = serverOptions.value[0].keyId;
          fetchData();
          fetchSystemConfigData();
        }
      })
      .catch(error => {
        console.error('Error fetching server options:', error);
      });
}
function fetchSelect(){
  fetchData();
  fetchSystemConfigData();
}
function fetchData() {
  if (!selectedServerId.value) {
    return;
  }

  axios.get(serviceAddress1+'/systemDeviceInfo', {params: {sid: selectedServerId.value}})
      .then(response => {
        serverData.value = response.data.data;
        dataLoaded.value = true;
      })
      .catch(error => {
        console.error('Error fetching data:', error);
      });
}

function fetchSystemConfigData() {
  if (!selectedServerId.value) {
    return;
  }
  axios.get(serviceAddress1+'/systemConfigInfo', {params: {sid: selectedServerId.value}})
      .then(response => {
        systemConfigData.value = response.data.data;
      })
      .catch(error => {
        console.error('Error fetching system configuration data:', error);
      });
}

function handleStartClick() {
  if (isStartButtonDisabled.value) {
    return;
  }

  axios.get(serviceAddress1+'/online_server', {params: {sid: selectedServerId.value}})
      .then(() => {
        window.location.reload();
      })
      .catch(error => {
        console.error('Error starting the server:', error);
      });
}

function handleStopClick() {
  if (isStopButtonDisabled.value) {
    return;
  }

  axios.get(serviceAddress1+'/offline_server', {params: {sid: selectedServerId.value}})
      .then(() => {
        window.location.reload();
      })
      .catch(error => {
        console.error('Error stopping the server:', error);
      });
}
</script>

<script>



import axios from 'axios';
const host = window.location.host; // 获取主机地址，例如：127.0.0.1:6005
const protocol = window.location.protocol; // 获取协议，例如：http 或 https

// 构建完整的服务地址
const serviceAddress = protocol + '//' + host;
export default {
  data() {
    return {
      isAddServerDialogVisible: false,
      isAddEnvironmentDialogVisible: false,
      isBackupDataDialogVisible: false,
      isDeleteServerDialogVisible: false,
      serverlistData: [],
      deleteId: null,
      ipSegments: ['', '', '', ''],
      serverIP: '',
      serverPort: '',
      logInfo: '',
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    handleIPInput(index) {
      this.ipSegments[index] = this.ipSegments[index].replace(/[^0-9]/g, '');
      if (this.ipSegments[index].length === 3 && index < 3) {
        this.$refs.ipInputs[index + 1].focus();
      }
    },
    handleBackspace(event, index) {
      if (event.key === 'Backspace' && this.ipSegments[index].length === 0 && index > 0) {
        this.$refs.ipInputs[index - 1].focus();
      }
    },
    handlePaste(event) {
      const pasteData = event.clipboardData.getData('text/plain').trim();
      const segments = pasteData.split('.');
      this.ipSegments = ['', '', '', ''];
      for (let i = 0; i < segments.length; i++) {
        this.$set(this.ipSegments, i, segments[i]);
      }
    },
    addServer() {
      const fullIPAddress = this.ipSegments.join('.');
      const serverAddress = `${fullIPAddress}:${this.serverPort}`;
      axios.post(serviceAddress+'/server_url', { url: serverAddress }, { timeout: 3000 })
          .then(response => {
            const timestamp = new Date().toLocaleString();
            const responseData = response.data;
            if (responseData.code === 200) {
              this.logInfo = `${timestamp}: 成功 - ${responseData.msg}\n` + this.logInfo;
            } else {
              this.logInfo = `${timestamp}: 错误 - ${responseData.msg}\n` + this.logInfo;
            }
          })
          .catch(error => {
            const timestamp = new Date().toLocaleString();
            if (error.code === 'ECONNABORTED') {
              this.logInfo = `${timestamp}: 请求超时,超过3s未响应请求,网络可能存在问题或服务被阻塞\n` + this.logInfo;
            } else {
              this.logInfo = `${timestamp}: 请求错误 - ${error.message}\n` + this.logInfo;
            }
          });
    },
    fetchData() {
      axios.get(serviceAddress+'/server_url')
          .then(response => {
            this.serverlistData = response.data.data.map(server => ({ ...server, visible: false }));
          })
          .catch(error => {
            console.error("Error fetching data:", error);
          });
    },
    confirmDelete(id) {
      axios.delete(`serviceAddress+'/server_url?sid=${id}`)
          .then(response => {
            if(response.data.code === 200){
              this.serverlistData = this.serverlistData.filter(server => server.keyId !== id);
            }
          })
          .catch(error => {
            console.error("Error deleting record:", error);
          });
    },
    showAddServerDialog() {
      this.isAddServerDialogVisible = true;
    },
    showAddEnvironmentDialog() {
      this.isAddEnvironmentDialogVisible = true;
    },
    showBackupDataDialog() {
      this.isBackupDataDialogVisible = true;
    },
    showDeleteServerDialog(){
      this.fetchData();
      this.isDeleteServerDialogVisible = true;
    }
  }
};
</script>


<style>
.box-card {
  margin: -20px 15% 0 15%;
  width: 70%;
}

.info-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.el-main {
  overflow-x: hidden;
}

.el-dropdown {
  vertical-align: top;
}

.el-dropdown + .el-dropdown {
  margin-left: 15px;
}

.el-icon-arrow-down {
  font-size: 12px;
}
.ip-input-group {
  display: flex;
  gap: 5px;
}

.ip-input {
  width: 100px; /* 根据需要调整每个输入框的宽度 */
}
</style>
