<script setup>
import {inject, computed} from 'vue';

// 从父组件注入systemConfigData
const systemConfigInfo = inject('systemConfigData');

// 计算星级
const serverLevelStars = computed(() => {
  return systemConfigInfo.value.level ? systemConfigInfo.value.level / 2 : 0;
});

// 显示等级的计算属性
const serverLevel = computed(() => {
  return serverLevelStars.value * 2;
});

// 获取状态图标的函数
const getStateIcon = (state) => {
  switch (state) {
    case 0:
      return 'el-icon-error';
    case 1:
      return 'el-icon-success';
    case 2:
      return 'el-icon-warning';
    default:
      return 'el-icon-info';
  }
};

// 计算服务器可用性的百分比
const serverAvailability = computed(() => {
  const frequency = systemConfigInfo.value.frequency;
  if (frequency < 10) {
    return 100 - frequency; // 频率越低，可用性越高
  } else if (frequency >= 10 && frequency <= 20) {
    return 90 - frequency * 2; // 频率在10到20之间，可用性适中
  } else {
    return Math.max(70 - frequency * 3, 0); // 频率超过20，可用性降低
  }
});
</script>

<template>
  <el-descriptions class="margin-top" :column="3" border>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-s-platform" style="color: deepskyblue"></i>
        <span style="font-weight: bold; font-family: 'Arial Black', 'Arial Bold', Gadget, sans-serif;">服务器地址</span>
      </template>
      <el-tag>{{ systemConfigInfo.baseUrl }}</el-tag>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-s-custom" style=" color: deepskyblue"></i>
        <span style="font-weight: bold; font-family: 'Arial Black', 'Arial Bold', Gadget, sans-serif;">服务器名称</span>
      </template>
      <el-tag>{{ systemConfigInfo.name }}</el-tag>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i :class="getStateIcon(systemConfigInfo.state)" style="color: deepskyblue"></i>
        <span style="font-weight: bold; font-family: 'Arial Black', 'Arial Bold', Gadget, sans-serif;">服务器状态</span>
      </template>
      <template v-if="systemConfigInfo.state === 0">
        <i class="el-icon-error" style="color: red;"></i> <!-- 停止状态的图标 -->
        <span>(停止)</span> <!-- 停止状态的文本 -->
      </template>
      <template v-else-if="systemConfigInfo.state === 1">
        <i class="el-icon-success" style="font-size: 20px; color: green;"></i> <!-- 启动状态的图标 -->
        <span>(启动)</span> <!-- 启动状态的文本 -->
      </template>
      <template v-else>
        <i class="el-icon-warning" style="color: orange;"></i> <!-- 暂停状态的图标 -->
        <span>(暂停)</span> <!-- 暂停状态的文本 -->
      </template>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-s-data" style=" color: deepskyblue"></i>
        <span style="font-weight: bold; font-family: 'Arial Black', 'Arial Bold', Gadget, sans-serif;">服务器等级</span>
      </template>
      <div style="display: flex; align-items: center;">
        <el-rate
            :value="serverLevelStars"
            disabled
            text-color="#ff9900">
        </el-rate>
        <span style="margin-left: 10px;">{{ serverLevel }} 级</span>
      </div>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-s-flag" style="color: deepskyblue"></i>
        <span
            style="font-weight: bold; font-family: 'Arial Black', 'Arial Bold', Gadget, sans-serif;">服务器可用性</span>
      </template>
      <el-progress :percentage="serverAvailability"></el-progress>
    </el-descriptions-item>
    <el-descriptions-item>

    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-bangzhu" style="color: deepskyblue"></i>
        <span style="font-weight: bold; font-family: 'Arial Black', 'Arial Bold', Gadget, sans-serif;">支持的语言</span>
      </template>
      <div class="tag-container" style="flex-basis: 100%;"> <!-- 使用 flex-basis: 100%; 让最后一个占满一行 -->
        <el-tag v-for="(language, index) in systemConfigInfo.languageLabelList" :key="index" class="tag">
          {{ language }}
        </el-tag>
      </div>
    </el-descriptions-item>
    </el-descriptions>
</template>



