<template>
  <el-card class="chart-card">
    <div class="chart-container">
      <div id="cpu-echart" ref="chart" style="height: 400px;"></div>
    </div>
  </el-card>
</template>

<script>
import { inject, onMounted, onUnmounted, ref, watch } from "vue";
import * as echarts from 'echarts';

export default {
  setup() {
    const chart = ref(null);
    const serverData = inject('serverData');

    onMounted(() => {
      chart.value = echarts.init(document.querySelector('#cpu-echart'));
      updateChart();
    });

    onUnmounted(() => {
      if (chart.value) chart.value.dispose();
    });

    watch(() => serverData.value, (newData) => {
      if (newData) {
        updateChart();
      }
    }, {deep: true}); // 使用 deep: true 来深度监听对象或数组

    function updateChart() {
      if (!chart.value || !serverData.value) {
        return;
      }

      // 从 serverData 中获取最新数据
      const latestData = serverData.value.slice(-1)[0] || {};
      const cpuTotalUsage = latestData.cpuTotalUsage ? latestData.cpuTotalUsage.toFixed(2) : 0;
      const cpuPhysicalCores = latestData.cpuPhysicalCores || 0;
      const cpuLogicalCores = latestData.cpuLogicalCores || 0;

      const options = {
        tooltip: {
          trigger: 'axis'
        },
        title: {
          text: 'CPU利用率（%）',
          subtext: `当前: ${cpuTotalUsage}% 物理核心: ${cpuPhysicalCores} 逻辑核心: ${cpuLogicalCores}`,
          left: 'right'
        },
        xAxis: {
          type: 'category',
          data: serverData.value.map(item => item.createTimeFormatted)
        },
        yAxis: {
          name: '%',
          type: 'value'
        },
        series: [{
          name: 'CPU使用率',
          type: 'line',
          areaStyle: {},
          data: serverData.value.map(item => item.cpuTotalUsage),
          smooth: true
        }]
      };

      chart.value.setOption(options);
    }

    return {
      chart
    };
  }
};
</script>

<style>
.chart-card {
  margin: 10px;
}

.chart-container {
  width: 100%;
  height: 400px;
}
</style>
