<template>
  <el-card class="chart-card">
    <div class="chart-container">
      <div id="diskIoChart" ref="chart" style="height: 400px;"></div>
    </div>
  </el-card>
</template>

<script>
import { inject, onMounted, onUnmounted, ref, watch } from "vue";
import * as echarts from 'echarts';

export default {
  setup() {
    const serverData = inject('serverData'); // 从父组件注入数据
    const chart = ref(null);

    onMounted(() => {
      chart.value = echarts.init(chart.value);
      updateChart();
    });

    onUnmounted(() => {
      if (chart.value) chart.value.dispose();
    });

    watch(() => serverData.value, (newData) => {
      if (newData) {
        updateChart();
      }
    }, { immediate: true, deep: true }); // 立即执行并深度监听

    function updateChart() {
      if (!chart.value || !Array.isArray(serverData.value)) {
        return;
      }

      // 从 serverData 中提取磁盘读取和写入数据
      const diskReadKbps = serverData.value.map(item => item.diskReadKbps);
      const diskWriteKbps = serverData.value.map(item => item.diskWriteKbps);

      const options = {
        title: {
          text: '系统盘IO（KB/s）',
          left: 'right'
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: serverData.value.map(item => item.createTimeFormatted)
        },
        yAxis: {
          name: 'KB/s',
          type: 'value'
        },
        series: [
          {
            name: '磁盘读取KB/s',
            type: 'line',
            data: diskReadKbps,
            smooth: true
          },
          {
            name: '磁盘写入KB/s',
            type: 'line',
            data: diskWriteKbps,
            smooth: true
          }
        ]
      };

      chart.value.setOption(options);
    }

    return {
      chart
    };
  }
};
</script>

<style>
.chart-card {
  margin: 10px;
}

.chart-container {
  width: 100%;
  height: 400px;
}
</style>
